window.addEventListener("DOMContentLoaded", () => {
    function showMenu(){
        let burgerBtn = document.querySelector("#nav-burger-btn"),
            navMenu = document.querySelector(".nav-burger"),
            closeBtn = navMenu.querySelector(".close-burger");
        burgerBtn.addEventListener("click", () => {
            navMenu.style.display = "block";
            document.body.style.overflow = "hidden";
            setTimeout(() => {
                navMenu.style.left = "0";
            }, 50);
        });
        closeBtn.addEventListener("click", () => {
            document.body.style.overflow = "auto";
            navMenu.style.left = "100%";
            setTimeout(() => {
                navMenu.style.display = "none";
            }, 250);
        });
        dropMenu();
        function dropMenu(){
            let arrLi = navMenu.querySelectorAll("ul>li");
            arrLi.forEach(elem => {
                if(elem.querySelector(".dop-menu")){
                    elem.querySelector("a").classList.add("has_menu");
                }
                elem.addEventListener("click", (e) => {
                    if(elem.querySelector(".dop-menu")){
                        if(!elem.classList.contains("has_click")){
                            e.preventDefault();
                            elem.classList.add("has_click");
                        }
                    }
                });
            });
        }
    }showMenu();

    function scrollHideNav(){
        // if(document.documentElement.clientWidth <= 768 || document.documentElement.clientHeight <= 768){
            let nav = document.querySelector("nav"),
                oldY = 0;
            window.addEventListener("scroll", (e) => {
                let y = pageYOffset;
                if(y > 0 && y > oldY){
                    nav.classList.add("out");
                }else nav.classList.remove("out");
                oldY = y;
            });
        // }
    }scrollHideNav();
});